import React from "react";
import SVG from "react-inlinesvg";
import {toAbsoluteUrl} from "../../_metronic/_helpers";
import { FormattedMessage} from "react-intl";
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@material-ui/core/TextField';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import IconButton from '@material-ui/core/IconButton';
import InputAdornment from '@material-ui/core/InputAdornment';

import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import FilledInput from '@material-ui/core/FilledInput';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import Select from '@material-ui/core/Select';
import NativeSelect from '@material-ui/core/NativeSelect';

import Button from '@material-ui/core/Button';
import { useHistory } from "react-router-dom";

export function AddBusiness() {

	const history = useHistory();
	function viewBusiness() {
	    history.push("/business/all");
	}
	function verifyBusiness() {
	    history.push("/business/verify");
	}

	const useStyles = makeStyles(theme => ({
		container: {
		    display: 'flex',
		    flexWrap: 'wrap',
		},
		textField: {
		    marginLeft: theme.spacing(1),
		    marginRight: theme.spacing(1),
		},
		dense: {
		    marginTop: theme.spacing(2),
		},
		menu: {
		    width: 200,
		},
	}));

	const classes = useStyles();
	const [values, setValues] = React.useState({
	    name: '',
	    user:'',
	    showPassword: false,
	    zone:'',
	    status:['active', 'inactive', 'disable'],
	});

	const handleChange = name => event => {
	    setState({ ...state, [name]: event.target.value, });
      setValues({ ...values, [name]: event.target.value });
	};

  	const [state, setState] = React.useState({
	    checkedA: true,
	    checkedB: true,
	    checkedC: true,
	    checkedD: true,
	    checkedE: true,
	    checkedF: true,
	});

	const registertype = [
    {
        value: '1',
        label: 'Corporate',
      },
      {
        value: '2',
        label: 'AOP',
      },
      {
        value: '3',
        label: 'Sole Proprietorship',
      },
      {
        value: '4',
        label: 'Sole Proprietorship',
      },
      {
        value: '5',
        label: 'Individual',
      }
  	];

	return (<>
		<div className="row">

    		<div role="alert" className="alert alert-custom alert-white alert-shadow gutter-b col-md-12 page-desc">
				<div className="alert-text">
					<span className="svg-icon menu-icon">
                    	<SVG src={toAbsoluteUrl("/media/svg/icons/Home/Building.svg")}/>
                  	</span> 
					<span>
						<FormattedMessage id="PAGE.ADDBUSINESS.DESC" />
					</span>
				</div>
			</div>

			<div className="card card-custom gutter-b col-md-12">
				<div className="card-header">
					<div className="card-title"><h3 className="card-label">Add Your Business Details</h3></div>
				</div>
		        <div className="card-body">
		        	<form className={classes.container} noValidate autoComplete="off">
		        		<div className="row">
			                <div className="col-md-6 ">
			                    <TextField
			                      required
			                        id="name"
			                        name="name"
			                        type="text"
			                        label="Official Business Name"
			                        className={classes.textField}
			                        value={values.name}
			                        onChange={handleChange('name')}
			                        margin="normal"
			                        variant="outlined"
        							helperText="Official Business Name Helping Text"
			                    />
			                </div>    
		                </div>
		                <div className="row">
			                <div className="col-md-6 ">
			                    <TextField
			                      required
			                        id="operating"
			                        name="operating"
			                        label="Operating as"
			                        type="text"
			                        className={classes.textField}
			                        value={values.operating}
			                        onChange={handleChange('operating')}
			                        margin="normal"
			                        variant="outlined"
        							helperText="Operating as Helping Text"
			                    />
			                </div>    
		                </div>
		                <div className="row">
			                <div className="col-md-6 ">
			                	<TextField
			                        select
			                        id="regtype"
			                        name="regtype"
			                        className={classes.textField}
			                        variant="outlined"
			                        label="Registration Type"
			                        margin="normal"
        							helperText="Registration Type Helping Text"
			                        value={values.weightRange}
			                        onChange={handleChange('weightRange')}
			                    >
			                        {registertype.map(option => (
			                          <MenuItem key={option.value} value={option.value}>
			                            {option.label}
			                          </MenuItem>
			                        ))}
			                    </TextField>
			                </div>    
		                </div>
		                <div className="row">
			                <div className="col-md-6 ">
			                    <TextField
			                      required
			                        id="regnumber"
			                        name="regnumber"
			                        type="text"
			                        label="Registration Number"
			                        className={classes.textField}
			                        value={values.operating}
			                        onChange={handleChange('regnumber')}
			                        margin="normal"
			                        variant="outlined"
        							helperText="Registration Number Helping Text"
			                    />
			                </div>    
		                </div>
		                <div className="row">
			                <div className="col-md-6 ">
			                    <TextField
  							        id="date"
  							        label="Registered On"
  							        name="regeistered_on"
  							        type="date"
  							        defaultValue=""
  							        className={classes.textField}
  							        margin="normal"
  							        variant="outlined"
        							helperText="Registered On Helping Text"
  							        InputLabelProps={{
  							          shrink: true,
  							        }}
  							    />
			                </div>    
		                </div>
		                <div className="row">
			                <div className="col-md-6 ">
			                    <TextField
  							        id="multiline-static"
  							        label="Head Office Address"
  							        multiline
  							        rows="3"
  							        defaultValue=""
  							        name="address"
  							        className={classes.textField}
  							        margin="normal"
  							        variant="outlined"
        							helperText="Head Office Address Helping Text"
  							    />
			                </div>    
		                </div>
		                <div className="row">
							<div className="col-md-6 class-submit">
	  					    	<Button variant="contained" color="primary" className={classes.button} onClick={verifyBusiness}>
		  					        Save 
		  					    </Button>
		  					    <Button variant="contained" className={classes.button} onClick={viewBusiness}>
		  					        Cancel
		  					    </Button>
	  					    </div>
		                </div>
						    
				          		
				    </form>
		        </div>
		    </div>   

		</div>
	</>);

}